@charset "UTF-8";

/* skip_Navi */
#skipNavi{
	position:relative;

	a{
		display:block;
		position:absolute;
		left:0;
		top:-5em;
		width:15em;
		text-align:center;
		color:#fff;
		background:#000;
		z-index:200;

		&:focus, 
		&:hover{
			top:0px;
		}
	}
}

/* toTop */
#btnTop{
	display:none;
	position:fixed;
	right:10px;
	bottom:10px;
	width:60px;
	height:60px;
	font-size:0;
	text-indent:-1000px;
	text-align:center;
	color:#fff;
	background:#fede5d;
	opacity:0.7;
	border-radius:50%;
	
	&::before{
		content: '';
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		margin: -16px 0 0 -17px;

		@include getSprite($common-btnTop);
	}

	&:hover, &:focus{
		opacity:1;
	}
}


body .ui-tooltip{font-size:12px;border-width:1px;box-shadow:none}
.alert_tip{position:absolute;color:#fff;background:#eb1c23;padding:3px 5px;border-radius:5px;}

#loading{position:fixed;top:0;bottom:0;left:0;right:0;background:rgba(0,0,0,0.6);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;z-index:500;}
#loading div{position:absolute;width:600px;height:36px;left:50%;top:50%;margin:-18px 0 0 -300px;overflow:visible;font-size:16px;}
#loading p{position:absolute;width:20px;height:36px;opacity:0;font-family:Helvetica, Arial, sans-serif;color:#35C4F0;text-align:center;
  animation:loadingMove 2s linear infinite;-o-animation:loadingMove 2s linear infinite;-moz-animation:loadingMove 2s linear infinite;-webkit-animation:loadingMove 2s linear infinite;
  transform:rotate(180deg);-o-transform:rotate(180deg);-moz-transform:rotate(180deg);-webkit-transform:rotate(180deg);}
#loading p:nth-child(2){animation-delay:0.2s;-o-animation-delay:0.2s;-moz-animation-delay:0.2s;-webkit-animation-delay:0.2s;}
#loading p:nth-child(3){animation-delay:0.4s;-o-animation-delay:0.4s;-webkit-animation-delay:0.4s;-webkit-animation-delay:0.4s;}
#loading p:nth-child(4){animation-delay:0.6s;-o-animation-delay:0.6s;-moz-animation-delay:0.6s;-webkit-animation-delay:0.6s;}
#loading p:nth-child(5){animation-delay:0.8s;-o-animation-delay:0.8s;-moz-animation-delay:0.8s;-webkit-animation-delay:0.8s;}
#loading p:nth-child(6){animation-delay:1s;-o-animation-delay:1s;-moz-animation-delay:1s;-webkit-animation-delay:1s;}
#loading p:nth-child(7){animation-delay:1.2s;-o-animation-delay:1.2s;-moz-animation-delay:1.2s;-webkit-animation-delay:1.2s;}


@-webkit-keyframes loadingMove {
	0% {left:0;opacity:0;}
	35% {left:41%;transform:rotate(0deg);opacity:1;}
	65% {left:59%;transform:rotate(0deg);opacity:1;}
	100% {left:100%;transform:rotate(-180deg);opacity:0;}
}
@keyframes loadingMove {
	0% {left:0;opacity:0;}
	35% {left:41%;transform:rotate(0deg);opacity:1;}
	65% {left:59%;transform:rotate(0deg);opacity:1;}
	100% {left:100%;transform:rotate(-180deg);opacity:0;}
}

/* 버튼 */
.button_wrap{position:relative;margin-top:50px;text-align:center;overflow:hidden}
.button{
	display:inline-block;
	padding:0px 15px;
	line-height:38px;
	border:1px solid #505050;
	color:#222;
	background:#fff;
	text-align:center;
	vertical-align:middle;
	font-weight:600;
	box-sizing:border-box;
	font-size:14px;
	cursor:pointer;
	-webkit-appearance:none;
	-webkit-border-radius:0;

	&.h20{padding:0px 5px;font-size:11px;line-height:18px;}
	&.h30{padding:0px 10px;font-size:13px;line-height:28px;}
	&.h50{padding:0px 25px;font-size:16px;line-height:48px;}

	&.btn_gray{background:#222;color:#fff;}
	&.btn_org{background:#fff;border-color:#ee7039;color:#ee7039;}
	&.btn_red{background:#dd0000;border-color:#dd0000;color:#fff;}
	
	&.w100{width:100px;padding-left:0;padding-right:0;}
	&.w200{width:200px;padding-left:0;padding-right:0;}
}
.btn_sort{
	display:inline-block;
	border:1px solid #b4b4b4;
	vertical-align:middle;

	a{display:inline-block;padding:5px 20px;border-left:0px solid #b4b4b4;text-align:center;line-height:20px;font-size:0.875em;}
	a:first-child{border-left:0 none;}
	a.on{color:#fff;background:#dd0000;}
}

.btn_txt{
	position:relative;
	display:inline-block;
	color:#222222;
	font-size:16px;
	vertical-align:middle;
	line-height:40px;
	letter-spacing:-2px;

	span{
		position:relative;
		display:inline-block;
		color:#fff;
		background:#222222;
		width:40px;
		height:40px;
		text-align:center;
		border-radius:50%;
		margin-left:10px;
		text-indent:-2px;
		vertical-align:top;
		z-index:1;

		&:before{
			content:'';
			display:block;
			position:absolute;
			right:0;
			top:0;
			width:100%;
			height:100%;
			border-radius:50%;
			background:#222;
			transition:all 0.3s;
			z-index:-1;
		}
	}

	&:hover span:before{transform:scale(1.2);}

	&.btn_red span{
		background:#dd0000;

		&:before{background:#dd0000;}
	}

	&.h60{
		line-height:60px;
		font-size:24px;

		span{width:60px;height:60px;margin-left:15px;}
	}
}


.sel_list{
	display: inline-block;
	position:relative;
	border:1px solid #c8c8c8;
	background:#fff;
	vertical-align: middle;

	a{
		display:block;
		padding:7px;
		font-size:14px;
		overflow:hidden;
		padding-right:41px;

		span{float:left;}
		strong{float:right;}
	}

	> a{
		position:relative;
		padding-right:40px;

		&:after{
			content: '';
			display: block;
			position: absolute;
			right: 15px;
			top:50%;
			margin-top: -3px;			
			@include getSprite($common-sel_list);
		}
	}

	ul{
		display:none;
		position:absolute;
		left:-1px;
		right:-1px;
		border:1px solid #c8c8c8;
		border-top:0 none;
		top:100%;
		width:100%;
		background:#fafafa;

		a:hover,
		a:focus{background:#eee;}

		.on a{font-weight:bold;color:#222;}
	}

	&.on ul{display:block;}
}


/* icon */
.ic_txt{
	display:inline-block;
	border:1px solid #4c4c4c;
	text-align:center;
	color:#fff;
	padding:0px 5px;
	vertical-align:middle;
	background:#4c4c4c;
	font-size:0.875em;
	line-height:1.3;

	&.ic_wht{color:#dd0000;border-color:#dd0000;background:#fff;}
	&.ic_red{background:#dd0000;border-color:#dd0000;}
	&.ic_green{background:#18a327;border-color:#18a327;}
}

.ic_state{
	display:inline-block;
	min-width:60px;
	text-align:center;
	color:#fff;
	padding:3px 5px;
	vertical-align:middle;
	background:#505050;
	font-size:0.875em;
	font-style:normal;
	border-radius:15px;

	&.red{background:#dd0000;}
	&.yellow{background:#ffc000;color:#505050;}
	&.blue{background:#3fb0ff;}
}


/* 탭메뉴 */
.tabmenu{
	margin-bottom:40px;
	text-align:center;

	> li{
		display:inline;

		> a{
			display:inline-block;
			width:158px;
			padding:1em 0;
			text-align:center;
			background:#3c3c3c;
			color:#fff;
			font-weight:bold;
		}

		> a:hover,
		> a:focus,
		> a:active,
		&.on > a{color:#fff;background:#dd0000;}
	}
}
.tabmenu2{
	margin-bottom:30px;
	> li{
		float:left;
		width:50%;

		> a{
			position:relative;
			display:block;
			padding:0.5em 0;
			border:1px solid #eeeeee;
			border-bottom-color:#dd0000;
			text-align:center;
			background:#eee;
			font-weight:bold;
			color:#666666;
		}

		&.on > a{
			color:#222222;
			background:#fff;
			border-color:#dd0000;
			border-bottom-color:#fff;
		}
	}
}
.tab_cont, .rdo_cont{
	display:none
	&.on{display:inherit}
}
