}


/*테이블*/
.table_wrap{}
.table{
	border-top:1px solid #c8c8c8;
	table-layout:fixed;

	th, td{border-bottom:1px solid #c8c8c8;text-align:center;}
	th{padding:15px 20px;color:#222;}
	td{padding:10px 20px;word-break:break-all;}
	thead th{border-top-color:#666;}
	tfoot th, tfoot td{background:#f3f3f3;}
	img{max-width:100%;}
	.bg_gray th, .bg_gray td{background:#f0f0f0;}
	.none{padding-top:2em !important;padding-bottom:2em !important;}

	&.tbl_left{
		td{border-left:1px solid #c8c8c8;text-align:left;}
		.first{border-left:0 none;}
	} 
	&.tbl_hover{
		tr:hover th,tr:hover td{background:#fafafa;}
	}
	&.tbl_form textarea{height:300px;}

	.t_center{text-align:center;}
	.t_left{text-align:left;}
	.t_right{text-align:right;}
	.bg{background:#f3f3f3;}
	.bg strong{color:#222;}

	.in_table {
		th,
		td{padding:5px;text-align:center;background:none;border:1px solid #ddd;border-width:1px 0;}
		th{color:#666;background:#fafafa;}
	}
}
.table2{
	th, td{
		border:1px solid #dddddd;
		border-right:0 none;
		padding:0.5em 0.3em;
		text-align:center;
	}
	th:first-child, td:first-child{border-left:0 none;}
	th{background:#f0f0f0;}
	tr:nth-child(odd) td{background:#fafafa;}
	.t_left{text-align:left;}
	.t_right{text-align:right;}
} 

.tbl_search{
	margin-bottom:5px;text-align:right;
	.select{width:70px;}
	.input{width:150px;}
}

/* list_view */
.list_view{
	border-top:1px solid #e6e6e6;
	> li{padding:15px 20px;border-bottom:1px solid #e6e6e6;overflow:hidden;}
	.tit{
		h3{display:inline;color:#505050;vertical-align:middle;}
		.date{float:right;display:inline-block;}
		.num{display:inline-block;}
		.ic_txt{margin-right:5px;}
	}
	.cont{
		padding:30px 20px;min-height:200px;word-break:break-all;

		img{
			max-width:100%
		}
		*{
			font-family:inherit !important;
		}
	}

	.img{
		text-align:center;margin-bottom:2em;
	}
	.link{
		border-top:1px solid #000000;
		
		a{
			display:block;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;
		}
		.ic_txt{
			margin-right:6px;
		}
	}

	+ .list_navi{
		border-top:0 none;
	}
}
.list_navi{
	border-top:1px solid #e6e6e6;

	li{
		padding:10px 20px;
		border-bottom:1px solid #e6e6e6;
		
		>a,
		>span{
			display:block;
			padding-left:36px;
			line-height:40px;
			overflow:hidden;
			white-space:nowrap;
			text-overflow:ellipsis;
			-o-text-overflow:ellipsis;
		}
	}

	.prev>a, 
	.prev>span{
		background:url(../images/common/list_prev.png) no-repeat left center;
	}
	.next>a, 
	.next>span{
		background:url(../images/common/list_next.png) no-repeat left center;
	}
}

/*페이징*/
.paging{
	clear:both;
	margin-top:2.5em;
	text-align:center;

	a{display:inline-block;}
	> img{opacity:0.4;}
	a img{opacity:1;}

	span{
		margin:0 10px;

		a, strong{
			display:inline-block;
			width:25px;
			line-height:25px;
			vertical-align:middle;
		}
		a:hover, a:focus, a:active,strong{
			color:#dd0000;font-weight:bold;
		}
	}
}


/* faq */
.faq_list{
	border:1px solid #000;border-width:1px 0;
	> li + li{
		border-top:1px solid #ccc;
	}
	> dt, > li > p{
		position:relative;padding:0 0 0 5.625em;border-top:1px solid #ccc;

		&:before{
			content:'Q';display:block;position:absolute;left:1em;top:0.4em;font-weight:bold;font-size:2em;line-height:1;color:#000;
		}
		&:first-child{
			border-top:0 none;
		}
		&.no_list{
			padding:1.875em 1.5em;text-align:center;background:none;color:#999;
		}
		&.on{
			background-color:#f7f7f7;
		}
		> a{
			display:block;padding:1.125em 1.5em 1.125em 0px;
		}
		> a:hover,
		> a:focus,
		> a:active,
		&.on > a{
			font-weight:bold;color:#222;
		}
	}

	> dd, > li > div{display:none;position:relative;padding:1.125em 1.5em 1.125em 5.625em;background:#f7f7f7;color:#333333;
		&:before{content:'A';display:block;position:absolute;left:1em;top:0.4em;font-weight:bold;font-size:2em;line-height:1;color:#dd0000;}
	}
}


/*etc*/
.m_img{display:none;}
.img_area{text-align:center;overflow:hidden;
	img{max-width:100%;}
}
.txt_list{padding-left:1.2em;
	li{margin-top:5px;list-style:disc;
		&:first-child{margin-top:0;}
	}
}
.indent_list li{padding-left:10px;text-indent:-10px}
.num_list{padding-left:1.5em;
	li{list-style-type:decimal;}
}
.dot_list li{position:relative;padding-left:13px;margin-bottom:5px;
	&:after{content:'';width:2px;height:2px;position:absolute;left:0;top:10px;border-radius:100%;background:#666;}
}
.boxStyle{padding:15px 12px;border:1px solid #d0d2d8;}

.no_img{position:relative;background:#eee;text-align:center;
	img{max-width:100%;opacity:0;filter:alpha(opacity=0);}
	span{display:inline-block;width:120px;height:18px;position:absolute;left:50%;top:50%;margin:-9px 0 0 -60px;text-indent:-999em;font-size:0;color:transparent;opacity:0.5;
		@include getSprite($common-logo);
		filter:url(filters.svg#gray);
		filter:gray;
		-webkit-filter:grayscale(1);}
}
