@charset "UTF-8";

@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');
@import url('http://fonts.googleapis.com/earlyaccess/notosanskr.css');

/* base */
body, h1, h2, h3, h4, h5, h6, fieldset,ul, ol, dl, dt, dd, p, button ,figure{margin:0;padding:0;}
body{font:normal 16px/1.5 'Noto Sans KR', Apple SD Neo Gothic, "Apple SD Gothic Neo", AppleSDGothicNeo, Roboto, Droid sans, Microsoft NeoGothic, malgun gothic, sans-serif, Verdana;color:#666;letter-spacing:-1px;}
legend, caption{width:0;height:0;visibility:hidden;font-size:0;line-height:0;}
a{text-decoration:none;color:inherit;}
img{vertical-align:middle;}
fieldset, button{background:none;border:0px;}
ul:after,ol:after{content:'';display:block;clear:both;}
li{list-style:none;}
table{width:100%;border-spacing:0px;border-collapse:collapse;font-size:1em;}
iframe{width:100%;vertical-align:middle;border:0 none;}
hr{margin:1.25em 0;border:0 none;height:1px;background:#c9c9c9;}
button, input, optgroup, select, textarea{font-family:inherit;}

/* Chrome, Safari용 스크롤 바 */
::-webkit-scrollbar{width:8px;height:8px;border:3px solid #fff;}
::-webkit-scrollbar-track{background:#efefef;-webkit-border-radius:10px;border-radius:10px;-webkit-box-shadow:inset 0 0 4px rgba(0,0,0,.2)}
::-webkit-scrollbar-thumb{height:50px;width:50px;background:rgba(0,0,0,.2);-webkit-border-radius:8px;border-radius:8px;-webkit-box-shadow:inset 0 0 4px rgba(0,0,0,.1)}

/* font-size */
.fz_dw{font-size:0.875em;}
.fz_up{font-size:1.125em;}

/* color */
.t_white{color:#fff}
.t_black{color:#222}
.t_red{color:#dd0000}
.t_orange{color:#ea7724}
.t_green{color:#69bd25}
.t_blue{color:#3fb0ff;}
.t_gray{color:#999}
.t_yellow{color:#ffc000;}

/* text-align */
.t_center{text-align:center;}
.t_left{text-align:left;}
.t_right{text-align:right;}

/* float */
.f_left{float:left;}
.f_right{float:right;}
.clear{clear:both;}

/* margin */
.mt5{margin-top:5px;}
.mt10{margin-top:10px;}
.mt15{margin-top:15px;}
.mt20{margin-top:20px;}
.mt25{margin-top:25px;}
.mt30{margin-top:30px;}
.mt35{margin-top:35px;}
.mt40{margin-top:40px;}
.mt45{margin-top:45px;}
.mt50{margin-top:50px;}

.mb5{margin-bottom:5px;}
.mb10{margin-bottom:10px;}
.mb15{margin-bottom:15px;}
.mb20{margin-bottom:20px;}
.mb30{margin-bottom:30px;}
.mb35{margin-bottom:35px;}
.mb50{margin-bottom:50px;}

.ml5{margin-left:5px;}
.ml10{margin-left:10px;}
.ml15{margin-left:15px;}
.ml20{margin-left:20px;}
.ml30{margin-left:30px;}

.mr5{margin-right:5px;}
.mr10{margin-right:10px;}
.mr15{margin-right:15px;}
.mr20{margin-right:20px;}
.mr30{margin-right:30px;}

/* width */
.w60{width:60px}
.w80{width:80px}
.w100{width:100px}
.w120{width:120px}
.w140{width:140px}
.w160{width:160px}
.w180{width:180px}
.w200{width:200px}
.w_50{width:50%}
.w_80{width:80%}
.w_100{width:100%}

.col_wid{position:relative;}
.col_wid:after{content:'';display:block;clear:both;}
.col_wid + .col_wid{margin-top:5px;}
.col_wid>*{display:block;float:left;width:100%;margin-left:2%;}
.col_wid>.button{padding-left:0;padding-right:0;}
.col_wid>*:first-child{margin-left:0;}
.col_wid.col2>*{width:49%;}
.col_wid.col3>*{width:32%;}
	.col_wid.col3 .wid2{width:66%;} 
.col_wid.col4>*{width:23.5%;}
	.col_wid.col4 .wid2{width:49%;}
	.col_wid.col4 .wid3{width:73.5%;}

/* etc */
.w_none{display:none;}
.block{display:block;}
.inblock{display:inline-block;}
.hide{display:none;}
.hidden{overflow:hidden;}
.blind{position:absolute;overflow:hidden;visibility:hidden;width:0px;height:0px;font-size:0;line-height:0;text-indent:-9999px;}
.ellipsis{display:block;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;-o-text-overflow:ellipsis;}
.op80{-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";filter:alpha(opacity=80);-moz-opacity:0.8;-khtml-opacity:0.8;opacity:0.8;}
.bgLayer{display:block;position:fixed;top:0;left:0;width:100%;height:100%;background:#333;opacity:0.6;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=60)";filter:alpha(opacity=60);-moz-opacity:0.6;-khtml-opacity:0.6;z-index:160;}
.wait-animation{-webkit-animation-play-state:paused;animation-play-state:paused;}

/*blur, grayscale */
.blur img{
    -webkit-filter:blur(6px);
    -moz-filter:blur(6px);
    filter:blur(6px);
}
.grayscale img{
    filter:url(filters.svg#gray);
	filter:gray;
	-moz-filter:grayscale(1);
	-ms-filter:grayscale(100%);
	-o-filter:grayscale(100%);
	-webkit-filter:grayscale(1);
}
a.grayscale:hover img, a.grayscale:focus img, a.grayscale:active img{
	filter:none;
	-moz-filter:none;
	-ms-filter:none;
	-o-filter:none;
	-webkit-filter:none;
}