@charset 'utf-8';
@media only screen and (max-width:1280px){
	.inner{
		width: auto;
	}
	#logo{
		height: 80px;
		width: 18%;
		border-bottom: 0 none;
		a{
			margin-top: 10px;
		}
	}
	.head_top ul{margin-left: 18%;}
	.gnb_wrap{
		height: auto;
		min-height: 70px;
		#gnb{
			margin-left: 18%;
			> ul{
				> li > ul{
					font-size: 18px;
				}
			}
		} 
	}

	#footer{
		padding-left: 10px;
		padding-right: 10px;
	}
}
@media only screen and (max-width:1024px){	
	#logo{
		width: 22%;
		a{
			margin-top: 15px;
			@include getSprite($common-logo_m);
			vertical-align: top;
			&:before{
				display: none;
			}			
		}
	}
	.head_top{		
		ul{
			margin-left:22%;
		}
	} 
	.gnb_wrap{
		#gnb{
			margin-left: 130px;
		} 
	}
	.sub{
		#logo{
			text-align: center;
			background: none;
			border: 0 none;
			a{
				position: relative;
				top: 50%;				
				vertical-align: top;
				transform: translateY(-50%);
				margin-left:0px;
				margin-top:0px;
				@include getSprite($common-sub_logo_m);
			}
		}
		.head_top{
			background: #ad0f14;
			ul > li{
				border: 0 none;
				strong{
					&.hd_order{
						@include getSprite($common-gnb_order_sub);
					}
					&.hd_login{
						@include getSprite($common-gnb_login_sub);
					}
					&.hd_cart{
						@include getSprite($common-gnb_cart_sub);
						i{
							color: #000;
							background: #fff;
							border-color: #ad0f14;
						}
					}
				}
			}
		}
		.btn_gnb{
			strong{				
				i:before,
				i:after,
				i{
					background: #fff;
				}
			}
		}
		.gnb_wrap{
			#gnb{
				margin-left:0;
				a{line-height: 1.5;}
				> ul{
					padding: 0;
					> li{
						display: none;
						&.active{
							display: block;
							> a{
								display: none;
							}
						}
						
						> ul{
							position: static;
							font-size: 24px;
							background: #e3e3e3;
							padding:15px 10px;
							> li{
								float:left;
								> a{
									padding:5px 10px;									
									color: #222222;
									&:after{
										display: none;
									}
								}
								&.active > a{
									color: #ad0f14;
									>span{
										&:after{
											margin: 0;
											background: #ad0f14;
										}
									}
								}
							}
						}
					}
				}
			} 
		}
	}

	.gnb_open .gnb_wrap{
		#gnb{
			position: fixed;
			top: 80px;
			left: 0;
			bottom: 0;
			right: 0;
			margin: 0;
			border-top: 1px solid #dbdbdb;
			overflow: auto;
			background: rgba(255,255,255,0.7);
			> ul{
				padding:15px 0 0;
				border: 0 none;
				font-size: 16px;
				background: #fff;
				left: 0;				
				> li{
					display: block;
					float: none;
					width: auto;					
					min-height:0;
					border-bottom: 1px solid #dbdbdb;
					> a{
						line-height: 50px;
						padding-left: 65px;
						&:before{
							content: '';
							display: block;
							position: absolute;
							left:15px;
							top:50%;
							transform: translateY(-50%);							
						}
						&:after{
							content: '';
							display: block;
							position: absolute;
							right:10px;
							top:50%;
							transform: translateY(-50%);
							@include getSpriteRetina($common-gnb_m_off);
						}
					}
					&.active a{
						display: block;
					}
					&.menu a:before{
						@include getSpriteRetina($common-gnb_m_menu);
					}
					&.promotion a:before{
						@include getSpriteRetina($common-gnb_m_promotion);
					}
					&.store a:before{
						@include getSpriteRetina($common-gnb_m_store);
					}
					&.customer a:before{
						@include getSpriteRetina($common-gnb_m_customer);
					}
					&.papajohns a:before{
						@include getSpriteRetina($common-gnb_m_papajohns);
					}
					&.franchise a:before{
						@include getSpriteRetina($common-gnb_m_franchise);
					}
					&.on a:after{
						@include getSpriteRetina($common-gnb_m_on);
					}					
					> ul{
						display: none;
						font-size: 13px;
						padding:0 10px 10px;
						> li{
							float: left;
							width: 50%;
							margin-top: 0px;
							> a{
								display: block;
								padding: 10px;
								line-height: 1;								
							}
							&.active > a:after{
								display: none;
							}
						}
					}
				}
			}
		}
	}

	.gnb_open .sub .gnb_wrap #gnb{
		border-top: 0 none;
		>ul{
			padding-top:0;
		}
	}


}