@charset 'utf-8';
@media only screen and (max-width:760px){
	#logo{
		height: 40px;
		a{
			margin-top: 8px;
			@include getSpriteRetina($common-logo_m);
		}
	}
	.head_top ul li a{
		padding: 0;
		line-height: 40px;
		strong{
			height: 27px;
			&.hd_order{
				@include getSpriteRetina($common-gnb_order);
			}
			&.hd_login{
				@include getSpriteRetina($common-gnb_login);
			}
			&.hd_cart{
				@include getSpriteRetina($common-gnb_cart);
				i{
					right: -7px;
    				top: -1px;
					width:14px;
					line-height: 14px;
					font-size: 10px;
					border-width: 2px;
				}
			}
		}
	}
	.btn_gnb{
		strong{
			width:20px;
			
			i:before,
			i:after,
			i{top:13px;height:2px;}
			i:before{top:-7px;}
			i:after{top:auto;bottom:-7px;}
		}
	}
	.gnb_wrap{
		min-height: 35px;
		#gnb{
			margin-left: 22%;
			> ul{
				font-size: 14px;
				padding: 0;
				> li > a{
					line-height: 35px;
					padding: 0 0.7em;
				}
			}
		} 
	}
	.sub{
		#logo{
			a{
				@include getSpriteRetina($common-sub_logo_m);
			}
		}
		.head_top{
			ul > li{
				strong{
					&.hd_order{
						@include getSpriteRetina($common-gnb_order_sub);
					}
					&.hd_login{
						@include getSpriteRetina($common-gnb_login_sub);
					}
					&.hd_cart{
						@include getSpriteRetina($common-gnb_cart_sub);
					}
				}
			}
		}
		.gnb_wrap{
			#gnb{
				> ul{
					> li{
						> ul{
							font-size: 12px;
							background: #e3e3e3;
							padding:7px 5px;
							> li{
								> a{
									padding:3px 7px;									
								}
							}
						}
					}
				}
			} 
		}
	}

	.gnb_open .gnb_wrap{
		#gnb{
			top: 40px;
			> ul{
				padding-top:7px;
			}
		}
	}

	#footer{
		padding: 10px;
		font-size: 10px
	}
	.foot_sns{
		text-align: center;
		li{
			margin:0 3px;
			a{
				padding: 7px;				
			}
			&.facebook span{
				@include getSpriteRetina($common-foot_facebook);
			}
			&.twitter span{
				@include getSpriteRetina($common-foot_twitter);
			}
			&.insta span{
				@include getSpriteRetina($common-foot_insta);
			}
			&.blog span{
				@include getSpriteRetina($common-foot_blog);
			}
		}
	}
	.foot_menu{
		padding-right: 0;
		text-align:center;
		li{
			a{
				font-size: 10px;
			}
		}
	}
	.foot_info{
		padding-right: 0px;
		text-align: center;
		ul{
			li{
				margin:0 0.2em;
			}
		}
	}
	.foot_etc{
		position: static;
		margin-top: 1em;
	}
	.foot_link{
		a{
			@include getSpriteRetina($common-foot_link_m);
		}
	}
	.foot_call{
		width: 290px;
		margin: 1em auto 0;
		li{font-size:10px}
	}
}