@charset "UTF-8";

/* input, select, textarea*/
.input, .textarea, .select{
    display:inline-block;
    height:40px;
    line-height:38px;
    padding:0px 10px;
    border:1px solid #aaaaaa;
    background:#fff;
    transition-duration:.1s;
    vertical-align:middle;
    box-sizing:border-box;
    
    &:hover, &:focus{
        border-color:#777
    }
    &:disabled{
        background-color:#eaeaea;
    }
}
.input[type="file"]{
    padding:3px;
    line-height:26px;
}
.textarea{
    width:100%;
    min-height:100px;
    padding:10px;
    resize:none;
    line-height:1.5;
}
.select{
    padding-right:0px;
}

/* checkbox, radio */
.checkbox, 
.radio{
    position:relative;
    display:inline-block;
    cursor:pointer;
    vertical-align:middle;

    input{
        opacity:0;
        position:absolute;
        left:0;
        top:0;
        width:30px;
        height:30px;
        z-index:1;

        &:checked,
        &:focus{
            outline:none! important
        }
        + .lbl{
            position:relative;
            display:inline-block;
            margin:0;
            line-height:18px;
            min-height:18px;
            padding:6px 0 6px 35px;
            font-weight:normal;
            vertical-align:top;
            word-break:break-all;

            &:before{
                content:'';
                border:1px solid #ddd;
                box-shadow:0 1px 2px rgba(0, 0, 0, 0.05);
                background:#ddd;
                display:inline-block;
                height:30px;
                width:30px;
                position:absolute;
                left:0;
                top:0;
                box-sizing:border-box;
                border-radius:100%;
            }

            &:after{
                content:'';
                display:inline-block;
                position:absolute;
                background-color:#fff
            }
        }
        &:checked + .lbl{
            color:#222;

            &:before{
                background:#dd0000;
            }
        }

        &:focus + .lbl:before{
            border-color:#777
        }

        &:disabled + .lbl:before,
        &.disabled + .lbl:before{
            opacity:0.5;
        }
    }
    
    &.only input + .lbl{
        padding-left:30px;
        text-indent:-999em;
        font-size:0;
    }

    
}
.checkbox input + .lbl:after{
    width:24px;
    height:10px;
    top:2px;
    left:6px;
    border:4px solid #fff;
    border-top:none;
    border-right:none;
    background:transparent;
    transform:rotate(-45deg);
}
.radio input + .lbl:after{
    width:14px;
    height:14px;
    top:8px;
    left:8px;
    border-radius:100%;
}

/* readonly, error, placeholder */
::-webkit-input-placeholder,
::-moz-placeholder,
::-ms-input-placeholde,
:-ms-input-placeholder,
.placeholder{
    color:#999;
    opacity:1;
}
.readonly{
    background:#f9f9f9 !important;
}
.error{
    background:#f9ecec !important;
}

/*파일첨부*/
.inp_file{
    position:relative;
    padding-right:105px;

    > input{
        width:100%;
        vertical-align:middle;
    }

    .btn_file{
        display:inline-block;
        position:absolute;
        right:0;
        top:0;
        overflow:hidden;
        vertical-align:middle;

        .button{
            width:100px;
            padding-left:0;
            padding-right:0;
        }

        input{
            position:absolute;
            left:0;
            top:0;
            width:100%;
            height:100%;
            padding:0;
            margin:0;
            font-size:10em;
            line-height:2;
            cursor:pointer;
            opacity:0;
            z-index:2;
        }
    }
}
span.inp_file{
    display:inline-block;
    padding-right:0;

    > input{
        width:400px;
    }

    .btn_file{
        position:relative;
    }
}

/*spinner*/
.ui-spinner{
    position:relative;
    display:inline-block;
    padding:0 45px;
    border:0 none !important;
    height:auto !important;

    .spinner{
        display:inline-block;
        width:60px;
        text-align:center;
        line-height:40px;
        height:40px;
        font-size:26px;
        color:#c70202;
        border:0;
        padding:0;
        font-weight:bold;
        vertical-align:top;
    }
}
.ui-spinner-button{
    position:absolute;
    top:0;
    width:36px;
    height:36px;
    overflow:hidden;

    .icon{
        position:absolute;
        left:-9999em;
        width:0px;
        height:0px;
        font-size:0;
        line-height:0;
        text-indent:-9999px;
    }
}
.ui-spinner-up{
    right:0;
    @include getSprite($common-spinner_plus);
}
.ui-spinner-down{
    left:0;
    @include getSprite($common-spinner_minus);
}

.inp_spinner{
    position:relative;
    display:inline-block;
    padding:0 45px;

    span{
        position: relative;
        display: inline-block;

        .input{
            display:inline-block;
            width:60px;
            text-align:center;
            line-height:40px;
            height:40px;
            font-size:1.5em;
            color:#c70202;
            border:0;
            padding:0;
            font-weight:bold;
            vertical-align:top;
        }
        .select{
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            padding: 0;
            font-size: 0.875em;
            text-align: center;
        }
    }
    .btn{
        position:absolute;
        top:0;
        width:36px;
        height:36px;
        
        &.btn_minus{
            left:0;
            @include getSprite($common-spinner_minus);
        }
        &.btn_plus{
            right:0;
            @include getSprite($common-spinner_plus);
        }
    }
}
/* datepicker */
.datepicker{
    width:140px;
}
.ui-datepicker-trigger{
    display:inline-block;
    width:32px;
    height:32px;
    margin-left:5px;    
    text-indent:-999em;
    vertical-align:middle;
    @include getSprite($common-btn_datepicker);
}

/* switch */
.switch{
    display:inline-block;
    vertical-align:middle;

    span{
        position:relative;
        width:65px;
        height:26px;
        background:#e8e8e8;
        display:inline-block;
        border-radius:13px;
        cursor:pointer;        
        transition:150ms linear;
        font-size:12px;

        &:before{
            content:"off";
            position:absolute;
            right:10px;
            top:5px;
            color:#999999;
            text-transform:uppercase;
            line-height:14px;
        }

        &:after{            
            content:"";
            display:block;
            height:22px;
            width:22px;            
            position:absolute;
            top:2px;
            left:2px;
            border-radius:999px;
            background:#fff;
            transition:150ms linear;
        }
    }

    input{
        position:absolute;
        left:-9999px;
        opacity:0;

        &:checked + span{
            background:#dd0000;

            &:before{
                content:"on";
                left:12px;
                color:#fff;
            }

            &:after{
                left:41px;
            }
        }
    }
}