@charset "UTF-8";

#wrap{
	min-width:320px;
}
.inner{
	position:relative;
	width:1280px;
	margin:0 auto;
}
#header{
	position:fixed;
	left:0;
	top:0;
	right:0;
	min-width: 320px;
	background: #fff;
	z-index:15;
}

#logo{
	position: absolute;
	left: 0;
	top: 0;
	width: 23.43%;
	height: 150px;
	border: 1px solid #dbdbdb;
	border-top: 0 none;
	text-align: center;
	background: #fff;
	box-sizing: border-box;
	z-index: 1;
	&:before{
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}
	a{
		display:inline-block;
		vertical-align: middle;
		@include getSprite($common-logo);
	}
}
.head_top{
	ul{
		margin-left: 23.43%;
		li{
			float: left;
			width: 25%;			
			box-sizing: border-box;
			border-right: 1px solid #dbdbdb;			
			a{
				display: block;
				padding: 13px 0;
				text-align: center;
				strong{
					position: relative;
					display: inline-block;
					height: 54px;
					vertical-align: middle;
					&.hd_order{
						@include getSprite($common-gnb_order);
					}
					&.hd_login{
						@include getSprite($common-gnb_login);
					}
					&.hd_cart{
						@include getSprite($common-gnb_cart);
						i{
							display: block;
							position: absolute;
							right: -15px;
							top: -2px;
							width: 24px;					
							line-height: 24px;
							text-align: center;
							color: #fff;
							background: #df1313;
							border: 3px solid #fff;
							border-radius: 50%;
							text-indent: 0;						
							font-size: 16px;
							font-style: normal;
						}
					}
				}
			}
		}
	}

}
.btn_gnb{
	strong{
		width:40px;
		i:before,
		i:after{content:'';transition-duration:0.3s, 0.2s, 0.2s;transition-delay:0.5s, 0s, 0s;}
		i:before,
		i:after,
		i{position:absolute;display:block;left:0px;top:25px;width:100%;height:4px;background:#000000;border-radius:1px;}
		i:before{top:-14px;-webkit-transition-property:top, -webkit-transform, transform;transition-property:top, -webkit-transform, transform;}
		i:after{top:auto;bottom:-14px;-webkit-transition-property:bottom, -webkit-transform, transform;transition-property:bottom, -webkit-transform, transform;}
	}
	.gnb_open &{	
		background: #f1f1f1;
		strong{
			i{background:none !important;-webkit-transition:background 0s 0.3s;-webkit-transition:background 0s 0.3s;transition:background 0s 0.3s}
			i:before,
			i:after{-webkit-transition-delay:0s, 0.3s, 0.3s;transition-delay:0s, 0.3s, 0.3s;background: #ad0f14 !important;}
			i:before{top:0px;width:100%;-webkit-transform:rotate(45deg);transform:rotate(45deg);}
			i:after{bottom:0px;width:100%;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);}
		}
	}
}
.gnb_wrap{
	background: #ad0f14;
	height:70px;
	#gnb{
		position: relative;
		margin-left: 23.43%;
		li{
			display: inline-block;
			&.mobile > a{
				display: none;
			}
			a{
				position: relative;
				display: inline-block;
				padding: 0 1em;
				color: #fff;
				font-weight: bold;
				line-height: 70px;
			}
		}
		> ul{
			position: relative;
			font-size:28px;
			padding: 0 1em;
			width:100%;
			box-sizing: border-box;
			transition:all 0.3s;
			> li{
				> ul{
					display: none;
					padding: 0 0.5em;
					font-size: 20px;
					> li > a{
						padding: 0 0.5em;
						
						> span{
							position: relative;
							display: inline-block;
							line-height: 1.5;
							&:after{
								content: '';
								display: block;
								height: 1px;							
								margin: 0 -0.5em
							}
						}
					}
					> li.active > a{
						&:after{
							content: '';
							display: block;							
							border-top: 14px solid #ad0f14;
							border-left: 13px solid transparent;
							border-right: 13px solid transparent;
							position: absolute;
							left: 50%;
							bottom: -14px;
							transform: translateX(-50%);
						}
						> span{
							&:after{
								background: #fff;							
							}
						}
					} 
				}
				&.active > ul{
					display: block;
				}
			}
		} 
		.sub &{
			> ul{
				padding: 0;
				> li{
					display: none;
					&.active{
						>a{display: none}
						display: block;
					}
				}
			}
		}
		.gnb_open &{
			> ul{
				position: absolute;
				top:0;
				left: -1px;
				right: 0;
				padding: 25px 30px;
				border: 1px solid #dbdbdb;				
				background: #fff;
				font-size: 24px;
				> li{
					display: block !important;
					float: left;
					width: 25%;
					min-height:200px;
					&.mobile > a{
						display: block;
					}
					> a{
						display: block !important;
						color: #ad0f14;
						line-height: 1;
						padding: 0;				
					}
					> ul{
						display: block;
						padding: 0;
						font-size: 16px;
						padding-top: 5px;
						> li{
							display: block;
							margin-top: 15px;				
							> a{
								display: block;
								padding: 0;
								color: #666666;
								line-height: 1.5;
								font-weight: normal;
							}
							&.active > a:after{
								display: none;
							}
						}
					}
				}
			}
		}
	}	
}
	
#container{
	
}
#contents{
	
}
.page_top{
	position:relative;
	padding:3.25em 50px;
	text-align:center;
	
	.tit{
		font-size:2.438em;
		color:#222222;
		line-height:1.2;
		animation-name:fadeInUp;
		animation-duration:2s;
	}
	.sub{
		font-size:1.5em;
		color:#dd0000;
		animation-name:fadeInUp;
		animation-duration:2s;
		animation-delay:0.2s;
	}
}


#footer{
	padding: 20px 0 30px;
	background: #ad0f14;
	font-size: 13px;
	color: #ff9c9f;
}
.foot_sns{
	li{
		display: inline-block;
		margin-right: 7px;
		a{
			display: inline-block;
			padding: 9px;
			background: #890b0f;
			border-radius: 50%;
			vertical-align:top;
			span{
				display: block;
				width: 22px;
				height: 22px;
				font-size: 0;
				color: transparent;
				transition: all 0.3s;
			}
			&:hover span{
				transform: scale(1.2);
			}
		}
		&.facebook span{
			@include getSprite($common-foot_facebook);
		}
		&.twitter span{
			@include getSprite($common-foot_twitter);
		}
		&.insta span{
			@include getSprite($common-foot_insta);
		}
		&.blog span{
			@include getSprite($common-foot_blog);
		}
	}
}
.foot_menu{
	margin-top:1em;
	padding-right: 340px;
	li{
		position: relative;
		display:inline-block;
		padding:0 1em;
		a{
			display:inline-block;
			color:#fff;
			vertical-align:top;
			font-weight:bold;
			font-size: 14px;
		}
		+ li:before{
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 25%;
			height: 50%;
			width: 1px;
			background: #fd6f74;
		}
		&:first-child{
			padding-left: 0;
		}
	}
}
	
.foot_info{
	margin-top: 1em;
	padding-right: 340px;
	ul{
		margin-bottom: 0.3em;
		li{
			display:inline-block;
			margin-right:0.5em;
			vertical-align: top;
			&:first-child{
				padding-left:0;
			}
		}
	}
}

.foot_etc{
	position: absolute;
	right: 0;
	top: 0;
}
.foot_link{
	text-align: center;
	a{
		display:inline-block;
		@include getSprite($common-foot_link);
	}
}
.foot_call{
	margin-top: 1.5em;
	li{
		float: left;
		width: 33.33%;
		text-align: center;
		font-size: 14px;
		color: #fff;
	}
}